



















































import { AppService } from "@/service/app.service";
import { ApiService } from "@/x-vue/services/api.service";
import Vue from "vue";
import { Component } from "vue-property-decorator";
import AccountSvg from "@/components/svg/AccountSvg.vue";
@Component({
  components: {
    AccountSvg,
  },
})
export default class extends Vue {
  app = AppService.instance;
  loading = true;
  passloginOk = false;
  async mounted(): Promise<void> {
    const code = this.$route.query.code;
    const state = this.$route.query.state;

    if (!code) {
      this.app.error("휴대폰번호 PASS 로그인 코드를 받지 못하였습니다.");
      return;
    }

    try {
      const user = await ApiService.instance.passlogin({ code: code });
      // 여기까지 오면, 패스로그인 성공.
      this.passloginOk = true;
      console.log("user; ", user);
      location.href = "https://" + state + ":" + location.port + "/passlogin/success";
    } catch (e) {
      if (typeof e === "string") {
        if (e.indexOf("Invalid authorization code") > 0) {
          this.app.error("휴대폰번호 PASS 로그인 인증 코드가 올바르지 않습니다.");
        }
      } else {
        this.app.error(e);
      }
    }
    this.loading = false;
  }
}
